import type { LitElement } from 'lit';

type Constructor<T = object> = new (...args: any[]) => T;
declare class ScrollResetMixinInterface {
  scrollResetDocument(options): void;
  scrollResetElement(el, options): void;
}
const scrollToDefaultOptions = {
  top: 0,
  left: 0
};
export const ScrollResetMixin = <T extends Constructor<LitElement>>(superClass: T) => {
  class ScrollResetEl extends superClass {
    // eslint-disable-next-line class-methods-use-this
    scrollResetDocument(options) {
      this.scrollResetElement(window, options);
    }

    scrollResetElement(el: HTMLElement | Window = this, options) {
      requestAnimationFrame(() => {
        el.scrollTo({ ...scrollToDefaultOptions, ...options });
      });
    }
  }
  return ScrollResetEl as Constructor<ScrollResetMixinInterface> & T;
};
